import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import $ from 'jquery';

import BasketProduct from './BasketProduct';
import './BasketProducts.css';

class int_BasketProducts extends React.PureComponent {

  static propTypes = {
    sign: PropTypes.bool.isRequired, // –ø–æ–ª—É—á–µ–Ω–æ –∏–∑ Redux
    user: PropTypes.object.isRequired, // –ø–æ–ª—É—á–µ–Ω–æ –∏–∑ Redux
  };


  state = {
    selectedItem: this.props.user,
  }

  render() {

    if(this.props.sign) {
      let arr = [...this.props.user.infoUser.basketProducts];
      var productsSelected=arr.map( product =>
        <BasketProduct key={product.id} info={product}  />
      );
      var controlLength=arr.length;
      var qty = arr.reduce((sum, elem)=>sum+elem.quantity, 0);
      var sum = arr.reduce((sum, elem) =>sum+elem.quantity*elem.price,0);
    }

  

    return (
      <div>
        <h1 className='NameCompany'>–ò–Ω—Ç–µ—Ä–Ω–µ—Ç&ndash;–º–∞–≥–∞–∑–∏–Ω &laquo;–ù–æ—É—Ç–±—É–∫–∏ –¥–ª—è –≤—Å–µ—Ö&raquo;</h1>
        { 
        this.props.sign&&
        <div>
        <div className='Basket'>–ö–æ—Ä–∑–∏–Ω–∞</div>
        {
          !controlLength==0&&
          <div className='Total'>–í –≤–∞—à–µ–π –∫–æ—Ä–∑–∏–Ω–µ {qty} —Ç–æ–≤–∞—Ä–æ–≤ –Ω–∞ —Å—É–º–º—É {sum} —Ä—É–±–ª–µ–π.</div>
        }
        <div className='LaptopProducts'>{productsSelected}</div>
        </div>
        }
        {
          controlLength==0&&
          <h2 className='Registr'>–í–∞—à–∞ –∫–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞.</h2>
        }
        {
          !this.props.sign&&
          <h2 className='Registr'>–í—ã –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã. –ü—Ä–æ–π–¥–∏—Ç–µ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –∏–ª–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é.</h2>
        }
      </div>
    );
  }
}


const mapStateToProps = function (state) {
  return {
    sign: state.signIn.stateIn,
    user: state.dataUser,
   };
};
    
const BasketProducts = connect(mapStateToProps)(int_BasketProducts);

export default BasketProducts;


