import React from 'react';
import PropTypes from 'prop-types';
import { NavLink } from 'react-router-dom';
import {connect} from 'react-redux';

import './PagesLinks.css';

class int_PagesLinks extends React.Component {

  static propTypes = {
    sign: PropTypes.bool.isRequired, // –ø–æ–ª—É—á–µ–Ω–æ –∏–∑ Redux
  };
  
  
  render() {

    return (
      <div className='HeaderLinks'>

        <NavLink to="/" className={obj => ("PageLink"+(obj.isActive?" ActivePageLink":""))}>–ì–ª–∞–≤–Ω–∞—è</NavLink>

        <NavLink to="/products" className={obj => ("PageLink"+(obj.isActive?" ActivePageLink":""))}>–¢–æ–≤–∞—Ä—ã</NavLink>
        { 
        !this.props.sign&&
        <NavLink to="/sign_up" className={obj => ("PageLink"+(obj.isActive?" ActivePageLink":""))}>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</NavLink>
        }
        { 
        !this.props.sign&&
        <NavLink to="/sign_in" className={obj => ("PageLink"+(obj.isActive?" ActivePageLink":""))}>–í–æ–π—Ç–∏</NavLink>
        }
        
        <NavLink to="/basket" className={obj => ("PageLink"+(obj.isActive?" ActivePageLink":""))}>–ö–æ—Ä–∑–∏–Ω–∞</NavLink>
        
        
        <NavLink to="/contacts" className={obj => ("PageLink"+(obj.isActive?" ActivePageLink":""))}>–ö–æ–Ω—Ç–∞–∫—Ç—ã</NavLink>

      </div>
    );   
  }
}
    


const mapStateToProps = function (state) {
  return {
    sign: state.signIn.stateIn,
   };
};
    
const PagesLinks = connect(mapStateToProps)(int_PagesLinks);

export default PagesLinks;



