import React from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';

import './LaptopProductInfo.css';

class int_LaptopProductInfo extends React.PureComponent {

  static propTypes = {
    sign: PropTypes.bool.isRequired, // –ø–æ–ª—É—á–µ–Ω–æ –∏–∑ Redux
    user: PropTypes.object.isRequired, // –ø–æ–ª—É—á–µ–Ω–æ –∏–∑ Redux
    info:PropTypes.shape({
      id: PropTypes.number.isRequired,
      nameProduct: PropTypes.string.isRequired,
      description: PropTypes.string.isRequired,
      price: PropTypes.number.isRequired,
    }),
  };

  state={
    limiter: false,
  }

  incCounter = (EO) => {
    if(!this.props.sign) alert('–í—ã –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã. –ü—Ä–æ–π–¥–∏—Ç–µ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –∏–ª–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é.');
    if(this.props.sign) this.props.dispatch( { type:"INC_PRODUCT", incProduct: {...this.props.info} } );
  }

  decCounter = () => {
    let qty=0;
    if(this.props.sign) {
      let arr =this.props.user.basketProducts;
      arr.forEach(elem=>{
        if(elem.id==this.props.info.id)  qty=elem.quantity; 
      })
    }
    if(!this.props.sign) alert('–í—ã –Ω–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω—ã. –ü—Ä–æ–π–¥–∏—Ç–µ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏—é –∏–ª–∏ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏—é.');
    if(this.props.sign&&qty) this.props.dispatch( { type:"DEC_PRODUCT", decProduct: {...this.props.info} } );
  }

  render() {
    let qty=0;
    if(this.props.sign) {
      let arr =this.props.user.basketProducts;
      arr.forEach(elem=>{
        if(elem.id==this.props.info.id)  qty=elem.quantity; 
      })
    }


    return (
      <div className='LaptopContainerInfo'>
        <div className="NameProductInfo">–ù–æ—É—Ç–±—É–∫ {this.props.info.nameProduct}</div>
        <div className='LaptopProduct'>
          <div className='ImageLaptopInfo'>
            <img src={`../images_1/${this.props.info.nameProduct.replace(/\s/g, '_')}.jpeg`}></img>
          </div>
          <div className='VerticalContainer'>
            <div className='ProductDescriptionInfo'>{this.props.info.description}</div>
            <div className='DetailedInfoProduct'>–ö–æ–¥ —Ç–æ–≤–∞—Ä–∞: {this.props.info.id}</div>
            <div className='DetailedInfoProduct'>–¢–µ—Ö–Ω–æ–ª–æ–≥–∏—è —ç–∫—Ä–∞–Ω–∞: {this.props.info.screenTechnology}</div>
            <div className='DetailedInfoProduct'>–ú–æ–¥–µ–ª—å –ø—Ä–æ—Ü–µ—Å—Å–æ—Ä–∞: {this.props.info.processorModel}</div>
            <div className='DetailedInfoProduct'>–û–±—ä–µ–º –æ–ø–µ—Ä–∞—Ç–∏–≤–Ω–æ–π –ø–∞–º—è—Ç–∏: {this.props.info.RAM}</div>
            <div className='DetailedInfoProduct'>–ö–æ–Ω—Ñ–∏–≥—É—Ä–∞—Ü–∏—è –Ω–∞–∫–æ–ø–∏—Ç–µ–ª—è: {this.props.info.driveConfiguration}</div>
            <div className='DetailedInfoProduct'>–í–∏–¥–µ–æ–∫–∞—Ä—Ç–∞: {this.props.info.video–°ard}</div>
            <div className='DetailedInfoProduct'>–û–ø–µ—Ä–∞—Ü–∏–æ–Ω–Ω–∞—è —Å–∏—Å—Ç–µ–º–∞: {this.props.info.OS}</div>
            <div className='DetailedInfoProduct'>–ù–∞–∑–Ω–∞—á–µ–Ω–∏–µ: {this.props.info.appointment}</div>
            <div className='ProductPriceInfo'>{this.props.info.price.toFixed(2)} —Ä—É–±</div>
            <div>
              <span className="PutBusket">–¥–æ–±–∞–≤–∏—Ç—å –≤ –∫–æ—Ä–∑–∏–Ω—É</span>
              <div className="CounterButton">
              <input type='button' value='-' onClick={this.decCounter} />
              <span className="CounterButtonValue">{qty}</span>
              <input type='button' value='+' onClick={this.incCounter} />
            </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


const mapStateToProps = function (state) {
  return {
    sign: state.signIn.stateIn,
    user: state.dataUser.infoUser,
   };
};
    
const LaptopProductInfo = connect(mapStateToProps)(int_LaptopProductInfo);

export default LaptopProductInfo;



